.page-main {
  width: 100%;
}

.page-main__section-full {
  margin-bottom: 120px;

  @media (max-width: $tablet-max-width) {
    margin-bottom: 70px;
  }

  @media (max-width: $mobile-max-width) {
    margin-bottom: 60px;
  }
}

.page-main__section {
  margin: 0 auto;
  padding-right: 98px;
  padding-left: 98px;
  width: 100%;
  max-width: 1366px;
  color: $black;

  @media (max-width: $tablet-max-width) {
    padding-right: 45px;
    padding-left: 45px;
  }

  @media (max-width: $mobile-max-width) {
    padding-left: 15px;
    padding-right: 15px;
  }

  &--new {
    margin-bottom: 113px;

    @media (max-width: $tablet-max-width) {
      margin-bottom: 62px;
    }

    @media (max-width: $mobile-max-width) {
      margin-bottom: 55px;
    }
  }

  &--blog {
    margin-bottom: 119px;

    @media (max-width: $tablet-max-width) {
      margin-bottom: 69px;
    }

    @media (max-width: $mobile-max-width) {
      margin-bottom: 60px;
    }
  }

  &--faq {
    margin-bottom: 108px;

    @media (max-width: $tablet-max-width) {
      margin-bottom: 72px;
    }

    @media (max-width: $mobile-max-width) {
      margin-bottom: 55px;
    }
  }

  &--gallery {
    margin-bottom: 131px;

    @media (max-width: $tablet-max-width) {
      margin-bottom: 80px;
    }

    @media (max-width: $mobile-max-width) {
      margin-bottom: 71px;
    }
  }

  h2 {
    @include cormorant;
    font-size: 34px;
    text-align: center;
    letter-spacing: 0.03em;
    line-height: 130%;

    span {
      font-size: 42px;
    }
  }

  ul {
    list-style: none;
    margin-top: 0;
  }
}

.page-main__container {
  position: relative;
}
